import { Box, Typography } from '@mui/material';
import React, { useEffect } from 'react';
import BasicLayout from '../components/BasicLayout';
import { Grid } from '@mui/material';
import TestImg from '../assets/img/main_1.png';
import { TextField, Button } from '@mui/material';

export default function Contact() {
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
        <BasicLayout title={'Contact Us'} subTitle={''}>
            <Box paddingX={{ xs: 3, md: 30 }} paddingY={10} maxWidth={2000} margin={'auto'}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>
                        <Box textAlign={'left'}>
                            <img src={TestImg} alt="test" style={{ width: '100%', maxWidth: '400px', height: '400px', margin: 'auto' }} />
                        </Box>
                        <Typography variant='h2' fontSize={40} color={'#5161b5'} marginTop={3} marginBottom={4}></Typography>
                        <Typography variant='p' fontSize={20} color={'#333'}>
                               . <br />
                            ,   . <br />
                            <br />
                                    . <br />
                             <span style={{ fontWeight: 'bold' }}>    .</span> <br />
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={6} >
                        <Box textAlign={'left'} boxShadow={2} width={'100%'} height={'100%'} paddingX={5} paddingY={5}>
                            <Box textAlign={'center'} marginTop={3} >
                                <Typography variant='h1' fontSize={35} color={'#5161b5'} marginTop={3} marginBottom={4} alignContent={'center'}></Typography>
                            </Box>

                            <form>
                                <TextField label="" fullWidth margin="normal" required />
                                <TextField label="" fullWidth margin="normal" required />
                                <TextField label="Email" fullWidth margin="normal" required />
                                <TextField label="" fullWidth margin="normal" required />
                                <TextField label="" fullWidth margin="normal" multiline rows={4} required />
                                <Box textAlign={'right'}>
                                    <Button variant="contained" color="primary" type="submit" sx={{ width: '100%', fontFamily: 'KboGothic_M', backgroundColor: '#3e53bd' }}></Button>
                                </Box>

                            </form>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </BasicLayout>
    );
}